$white: #ffffff;
$light_grey_bg: #F6F6F6;


$dark_grey_button: #292929;
$primary: #F1CDB3;
$primary_hover: #FDDCC4;

$non_active_card_bg: #FAFAFA;

$main_grey: #545454;
$text-about: #4C4C4C;
$light_grey_main-screen: #CDCDCD;
$light_grey_italic: #B2B2B2;



@font-face{
    font-family: 'Arial';
    src: url('../fonts/ARIAL.TTF');
    font-weight: normal;
    font-style: normal;
    }
    
@font-face{
    font-family: 'Arial';
    src: url('../fonts/ARIALI.TTF');
    font-weight: normal;
    font-style: italic;
    }
    
@font-face{
    font-family: 'Georgia';
    src: url('../fonts/GEORGIA.TTF');
    font-weight: normal;
    font-style: normal;
    }

* {
    padding: 0;
    margin: 0;
    
}

ul{
    list-style: none;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Georgia';
    font-style: normal;
    font-weight: normal;

}

.btn {
    width: 207px;
height: 52px;
border-radius: 100px;
border: 2px solid $primary;
box-sizing: border-box;
font-family: 'Georgia';
font-size: 17px;
line-height: 130%;
letter-spacing: 0.06em;
color: $dark_grey_button;
background: $primary;
transition: all ease-out 0.3s;
display: flex;
justify-content: center;
align-items: center;

    &:hover {
    background: $primary_hover;
    border-color: $primary_hover;
    cursor: pointer;
    transition: all ease-out 0.2s;
    }
}


.center {
    max-width: 1200px;
    margin: auto;
}

.start_screen {
    background: url('../images/noise_transparent2x.png'), radial-gradient(100% 215.42% at 0% 0%, #5B483A 0%, #262425 100%), #211F20;
    width: 100%;
    height: 908px;
}


header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 60px 0;
    
    .logo{
        .logo_title {
            font-family: 'Georgia';
            font-style: normal;
            font-weight: normal;
            font-size: 32px;
            line-height: 110%;
            letter-spacing: 0.06em;
            color: $primary;
            margin-bottom: 10px;
        }
        
        .logo_subtitle {
            font-family: 'Arial';
            font-size: 13px;
            line-height: 15px;
            letter-spacing: 0.1em;
            color: $white;
            text-align: center;
        }
        
    }
    
    .nav {
        display: flex;
        padding-bottom: 2px;

        
        .nav_item {
            margin-left: 35px;
            margin-right: 1px;
            
        }
        
        .nav_link {
            font-family: 'Arial';
            font-size: 15px;
            line-height: 160%;
            color: $light_grey_main-screen;
            text-decoration: none;

            display: block;
            
            &:hover {
                color: $non_active_card_bg;
            }
            
            &.nav_link--active {
                color: $non_active_card_bg;
    
                border-bottom: 3px solid $primary;
            }
        }
        
    }
    
    
        
}

.start_screen_wrapper {
    display: flex;
    justify-content: space-between;
    
    .start_screen_text {
        margin-top: 163px;
    }
    
    .start_screen_header {
        font-size: 44px;
        font-weight: normal;
        line-height: 130%;
        color: $white;
        margin-bottom: 41px;
        
    }
    .start_screen_description {
        font-family: 'Arial';
        font-size: 15px;
        line-height: 160%;
        color: $light_grey_main-screen;
        margin-bottom: 41px;
    }
    
    
}
.about_shelter {
    height: 588px;
    box-sizing: border-box;
}
.about_shelter_wrapper {
    padding: 80px 175px 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    .about_shelter_text {
        width: 430px;
    }
    
    .about_shelter_header {
        font-size: 35px;
        font-weight: normal;
        line-height: 130%;
        letter-spacing: 0.06em;
        color: $main_grey;
        margin-bottom: 24px;
    }
    .about_shelter_description {
        font-family: 'Arial';
        font-size: 15px;
        line-height: 160%;
        color: $text-about;
        margin-bottom: 25px;
    }
    
    .about_shelter_description:last-child {
        margin-bottom: 4px;
    }
    
}
.pets {
    background: $light_grey_bg;
    padding: 80px 0 100px;
    height: 877px;
    box-sizing: border-box;
    
    
    .pets_header {
        font-size: 35px;
        font-weight: normal;
        line-height: 130%;
        text-align: center;
        letter-spacing: 0.06em;
        color: $main_grey;
        margin-bottom: 60px;
    }
    .pets_slider {
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 59px;
        
        .pet_slider_arrow {
            width: 52px;
            height: 52px;
            background: transparent;
            margin: auto 0;
            &:hover {
                background: $primary_hover;
            }
            
            &.to_left {
                transform: scale(-1, 1) 
            }
        }
        
    }
    
    .pets_cards  {
        display: flex;
        justify-content: space-between;
        
        .pet_card {
            background: $non_active_card_bg;
            width: 270px;
            height: 435px;
            padding: 29px 35px 31px;
            margin-right: 90px;
            box-sizing: border-box;
            border-radius: 9px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            
            &:hover, &.--active {
                box-shadow: 0px 2px 35px 14px rgba(13, 13, 13, 0.04);
                background: $white;
                cursor: pointer;
            }
            
            &:last-child {
                margin-right: 0;
            }
            
            
            .pet_photo {
                width: 200px;
                height: 240px;
                margin-bottom: 29px;
            }
            .pet_name {
                font-family: 'Georgia';
                font-size: 20px;
                line-height: 23px;
                letter-spacing: 0.06em;
                color: $main_grey;
                margin-bottom: 30px;
                text-align: center;
            }
            .pet_card_btn {
                width: 187px;
                height: 52px;
                background: transparent;
                margin: 0 auto;
                
                &:hover {
                    background: $primary_hover;
                }
            }
        }
        
        
    }
    .pet_slider_rest {
            width: 261px;
            margin: 0 auto;
        }
    
    
}

.help {
    padding: 79px 0 101px;
    box-sizing: border-box;


    
    .help_header {
        font-weight: normal;
        font-size: 35px;
        line-height: 130%;
        letter-spacing: 0.06em;
        color: $main_grey;
        text-align: center;
        margin-bottom: 59px;
    }
    
    .help_items_wrapper {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
      
        
        .help_item {
        
            margin-right: 120px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            
            .help_icon {
                height: 60px;
                width: auto;
                margin-bottom: 29px;
            }
            
            .help_title {
                font-weight: normal;
                font-size: 20px;
                line-height: 115%;
                letter-spacing: 0.06em;
                color: $main_grey;
            }
            
        }
        .help_item:nth-child(5), 
        .help_item:nth-child(9) {
            margin-right: 0px;
        }
        .help_item:nth-child(1),
        .help_item:nth-child(2),
        .help_item:nth-child(3),
        .help_item:nth-child(4),
        .help_item:nth-child(5) {
            margin-bottom: 56px;
        }
        
    }
    
}
.donation {
    background: $light_grey_bg;
    padding: 82px 0 100px;
    
    .donation_wrapper {
        display: flex;
        align-items: center;
        padding: 0 143px 0 141px;
        justify-content: space-between;
        
        .donation_photo_dog {
            width: 505px;
            height: 261px;
            margin-right: 30px;
            padding-top: 1px;
        }
        
        
        .donation_header  {
            font-weight: normal;
            font-size: 35px;
            line-height: 130%;
            letter-spacing: 0.06em;
            margin-bottom: 19px;
            color: $main_grey;
        }
        
        .donation_bank_name {
            font-weight: normal;
            font-size: 15px;
            line-height: 110%;
            letter-spacing: 0.06em;
            color: $main_grey;
            margin-bottom: 20px;
            
        }
        
        .donation_credit_card {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 349px;
            height: 43px;
            border-radius: 9px;
            background: $primary;
            padding: 10px 14px 10px 15px;
            box-sizing: border-box;
            margin-bottom: 20px;
            
            .donation_credit_card_numbers {
                font-weight: normal;
                font-size: 20px;
                line-height: 115%;
                letter-spacing: 0.06em;
                color: $main_grey;
            }
            
        }
        .donation_description {
            font-family: 'Arial';
            font-style: italic;
            font-weight: normal;
            font-size: 12px;
            line-height: 18px;
            color: $light_grey_italic;
        }
        
        
        
    }
    
}

footer {
    background: url('../images/noise_transparent2x.png'), radial-gradient(110.67% 538.64% at 5.73% 50%, #513D2F 0%, #1A1A1C 100%), #211F20;
    padding-top: 41px;
    height: 355px;
    box-sizing: border-box;
    overflow: hidden;
    
    .footer_wrapper {
        display: flex;
    }
    .footer_contacts {
        margin-right: 160px;
    }
    .footer_location {
        margin-right: 167px;
    }
    
    .footer_questions, .footer_visit {
        font-weight: normal;
        font-size: 35px;
        line-height: 130%;
        letter-spacing: 0.06em;
        color: $white;
        margin-top: 22px;
    }
    .footer_questions,
    .footer_contacts_email {
        margin-bottom: 39px;
    }
    .footer_visit,
    .footer_location_address:first-of-type {
        margin-bottom: 32px;
    }
    
    .footer_contacts_email,
    .footer_contacts_phone,
    .footer_location_address {
        display: flex;
      align-items: center;
    }
    
    
    .footer_email,
    .footer_phone,
    .footer_address {
        font-weight: normal;
        font-size: 20px;
        line-height: 115%;
        letter-spacing: 0.06em;
        color: $primary;
        margin-left: 20px;
    }
    
    .footer_photo_puppy {
        width: 300px;
        height: 315px;
    }
    
}